body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
.topOfLayout {
  display: flex;
  justify-content: space-around;
  width: 80%;
}
.charts{
  display: flex;
  justify-content: space-around;
}

@media screen and (max-width:1200px){
  .topOfLayout{
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    min-height: 600px;
  }
  .charts{
    flex-direction: column;

  }
}

.ProductCard_Card__NTMKF {
    width: 60%;
    border:2px solid gray;
    border-radius: 5px; 
    box-shadow: 9px -2px 11px -1px #000000;
    margin-bottom: 30px;
}

.ProductCard_Card__NTMKF img{
    width: 30%;
}

.ProductCard_Prices__3YfIK{
    display: flex;
    justify-content: space-around;
}

.ProductCard_Prices__3YfIK p {
    font-weight: 600;
}

.ProductCard_Prices__3YfIK p:first-of-type{
    color: red;
    
}
.ProductCard_Prices__3YfIK p:nth-of-type(2){
    color: orange;
}

.ProductCard_Prices__3YfIK p:last-of-type{
    color: green;
}

@media screen and (max-width:1000px){
    .ProductCard_Card__NTMKF{
        width: 80%;
    }
    .ProductCard_Card__NTMKF img{
        width: 40%;
    }
    .ProductCard_Card__NTMKF p{
        padding: 0 50px;
    }
}

@media screen and (max-width:792px){
    .ProductCard_Prices__3YfIK p{
       padding: 0; 
    }
}
@media screen and (max-width:792px){
    .ProductCard_Card__NTMKF{
        width: 90%;
    }
    .ProductCard_Card__NTMKF p{
        padding:  0 2%;
    }
    .ProductCard_Prices__3YfIK {
       flex-direction: column; 
    }
    .ProductCard_Prices__3YfIK p {
        margin: 2%; 
     }
}
.Switcher_products__1irMx{
